import React from "react";
import { useTranslation } from "react-i18next";

export default function Contacto(){
     //Se encarga de obtener el idioma a usar en el texto
     const [t, i18n] = useTranslation("global");

    const numeroMaximoCaracteres=500;

    const [numeroCaracteres, setNumeroCaracteres]=React.useState(numeroMaximoCaracteres);
    const entradaCaracteres =(e:React.ChangeEvent<HTMLTextAreaElement>)=>{
        const caracteresUsados = e.target.value.length;
        setNumeroCaracteres(numeroMaximoCaracteres-caracteresUsados);
    }

    return <>
    <div id="Contacto" className="contactoContenedor apartado">
        <h2 className="subtitulo">{t("contacto.subtitulo")}</h2>
        <form className="formularioContenedor" action="https://formsubmit.co/19a613fe74a2e9da22ddbd872b5a07ed" method="POST" id="formularioContacto">
            <div className="campoContenedor">
                <label className="etiqueta" htmlFor="name">{t("contacto.nombre")}</label>
                <input className="entrada" type="text" id="name" name="name" required></input>
            </div>
           
            <div className="campoContenedor">
                <label className="etiqueta" htmlFor="email">{t("contacto.email")}</label>
                <input className="entrada" type="email" id="email" name="email" required></input>
            </div>
           
            <div className="campoContenedor mensajeContenedor">
                <label className="etiqueta"  htmlFor="mensaje">{t("contacto.mensaje")}</label>
                <label className="caracteres" htmlFor="mensaje">{numeroCaracteres}</label>
                <textarea onChange={entradaCaracteres} className="entrada mensaje" id="mensaje" name="textarea" minLength={10} maxLength={numeroMaximoCaracteres} required></textarea>
            </div>

            <button type="submit" className="enviar sombra">{t("contacto.enviar")}</button>

            <input type="hidden" name="_next" value="https://hectorrangel948.github.io/"></input>
            <input type="hidden" name="_captcha" value="false"></input>
        </form>
    </div>
    </>
}