import Carrusel from './Carrusel';
import { useTranslation } from "react-i18next";

import fv from "./../recursos/FV.jpg";
import cop from "./../recursos/copayment.jpg";
import alu from "./../recursos/alura.jpg";

export default function Proyectos(){

    //Se encarga de obtener el idioma a usar en el texto
    const [t, i18n] = useTranslation("global");
    
    //Arreglo con cada imagen del carrusel
    const imagenes=[fv, cop, alu];
    const tipoProyecto=[t("proyectos.tipoProyecto.profesional"), t("proyectos.tipoProyecto.profesional"), t("proyectos.tipoProyecto.academico")];
    //Arreglo con cada descripción del carrusel
    const proyectoFV=t("proyectos.descripcion.proyectoFV");
    const proyectoCP=t("proyectos.descripcion.proyectoCP");
    const proyectoAlu =t("proyectos.descripcion.proyectoAlu");
    const descripciones=[proyectoFV, proyectoCP,proyectoAlu];

    return <>
    <div id="Proyectos" className="proyectosContenedor apartado">
        <h2 className="subtitulo">{t("proyectos.subtitulo")}</h2>
        <Carrusel imagenes={imagenes} descripciones={descripciones} tipoProyecto={tipoProyecto} alto={250} ancho={450}/>
    </div>
    </>
}