import LinkedIn from './../recursos/iconos/Redes/LI.svg';
import GitHub from './../recursos/iconos/Redes/GH.svg';

export default function Footer(){

    return <>
        <footer id="Footer" className="apartado">
            <div className="redesContenedor">
                <a className="contenedorLogo" href="https://www.linkedin.com/in/hectorarangel/" target="_blank" rel="noreferrer"><img className="logoRedes" src={LinkedIn} alt="LinkedIn" id="LinkedIn"></img></a>
                <a className="contenedorLogo" href="https://github.com/HectorRangel948" target="_blank" rel="noreferrer"><img className="logoRedes" src={GitHub} alt="Github" id="GitHub"></img> </a>
            </div>
        </footer>
    </>
}