import React from "react";
import { useTranslation } from "react-i18next";

interface ICarrusel{
  imagenes : string[];
  descripciones : string[];
  tipoProyecto? : string[];
  alto? : number;
  ancho? : number;
  indice? : number;
}

const Carrusel : React.FC<ICarrusel>= ({imagenes, descripciones, tipoProyecto, alto, ancho, indice})=> {
  //Se encarga de obtener el idioma a usar en el texto
  const [t, i18n] = useTranslation("global");

  //Índice para cada imagen del carrusel
  const [index, setIndex]=React.useState(indice?indice:0);

  return (
    <>
      {tipoProyecto?<h3 className="tipoProyecto">{tipoProyecto[index]}</h3> : <></>}

      <div className="carruselContenedor">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="40"
          height="40"
          fill="currentColor"
          className="flechaCarrusel izquierda1 bi bi-caret-left-square"
          id="flechaIzquierda"
          viewBox="0 0 16 16"
          onClick={()=>
            {
              index<=0 ? setIndex(imagenes.length-1):setIndex(index-1); //Si el índice es menor o igual a cero se resetea con el último valor posible si no se reduce una unidad
            }
          }
        >
          <path d="M14 1a1 1 0 0 1 1 1v12a1 1 0 0 1-1 1H2a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1zM2 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2z" />
          <path d="M10.205 12.456A.5.5 0 0 0 10.5 12V4a.5.5 0 0 0-.832-.374l-4.5 4a.5.5 0 0 0 0 .748l4.5 4a.5.5 0 0 0 .537.082" />
        </svg>
        <img
          className="imagenCarrusel"
          alt="imagen de proyecto"
          src={imagenes[index]}
          height={alto}
          width={ancho}
          id="imagen"
        ></img>

        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="40"
          height="40"
          fill="currentColor"
          className="flechaCarrusel bi bi-caret-right-square"
          id="flechaDerecha"
          viewBox="0 0 16 16"
          onClick={()=>
            {
              index>=imagenes.length-1 ? setIndex(0):setIndex(index+1); //Si el índice es mayor o igual al tamaño del arreglo se resetea a 0 si no se incrementa una unidad
            }
        }
        >
          <path d="M14 1a1 1 0 0 1 1 1v12a1 1 0 0 1-1 1H2a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1zM2 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2z" />
          <path d="M5.795 12.456A.5.5 0 0 1 5.5 12V4a.5.5 0 0 1 .832-.374l4.5 4a.5.5 0 0 1 0 .748l-4.5 4a.5.5 0 0 1-.537.082" />
        </svg>
        <p id="descripcion" className="descripcion">
          {descripciones[index]}
        </p>
      </div>
    </>
  );
}   

export default Carrusel;
