import { useTranslation } from "react-i18next";

import HTMLIcon from './../recursos/iconos/Tecnologias/HTML.svg';
import CSSIcon from './../recursos/iconos/Tecnologias/CSS.png';
import JavaScriptIcon from './../recursos/iconos/Tecnologias/JavaScript.svg';
import ReactIcon from './../recursos/iconos/Tecnologias/React.svg';
import BootstrapIcon from './../recursos/iconos/Tecnologias/Bootstrap.png';
import Java from './../recursos/iconos/Tecnologias/JAVA.svg';
import MySQL from './../recursos/iconos/Tecnologias/Mysql.svg';
import Springboot from './../recursos/iconos/Tecnologias/Spring.png';
import NodeJS from './../recursos/iconos/Tecnologias/NodeJS.svg';
import Git from './../recursos/iconos/Tecnologias/Git.svg';
import GitBash from './../recursos/iconos/Tecnologias/GitBash.png';
import GitHub from './../recursos/iconos/Tecnologias/GitHub.svg';
import Jira from './../recursos/iconos/Tecnologias/Jira.svg';
import Figma from './../recursos/iconos/Tecnologias/Figma.svg';
import Consola from './../recursos/iconos/Tecnologias/CommandLine.png';
import Sass from './../recursos/iconos/Tecnologias/Sass.svg';

const Habilidades=()=>{
     //Se encarga de obtener el idioma a usar en el texto
    const [t, i18n] = useTranslation("global");
    
    return <>
    <div id="Habilidades" className="habilidadesContenedor apartado">
        <h2 className="subtitulo">{t("habilidades.subtitulo")}</h2>
        

            <div className="seccionesContenedor">
                <div className="tecnicas">
                    <h3 className="habilidad">{t("habilidades.habilidades.tecnicas")}</h3>
                    
                    <div className="tarjetasContenedor">

                    <div className="tarjeta sombra">
                        <div className="tituloContenedor">
                            <div className="titulo">{t("habilidades.titulos.frontEnd")}</div>
                        </div>
                        <div className="areas">
                            <div className="area">
                                {t("habilidades.areas.lenguajes")}
                            </div>
                            <div className="tecnologias">
                                <div className="iconosContenedor">
                                    <img  className="icono" src={HTMLIcon} alt="logo html"></img>
                                    <img  className="icono css" src={CSSIcon} alt="logo css"></img>
                                    <img  className="icono" src={JavaScriptIcon} alt="logo css"></img>
                                </div>
                                <div className="textoContenedor">
                                    <a className="texto">HTML</a>
                                    <a className="texto">CSS</a>
                                    <a className="texto">JavaScript</a>
                                </div>
                            </div>

                            <div className="area">
                                {t("habilidades.areas.librerias")}
                            </div>
                            <div className="tecnologias">
                                <div className="iconosContenedor">
                                    <img  className="icono" src={ReactIcon} alt="logo html"></img>
                                    <img  className="icono" src={BootstrapIcon} alt="logo css"></img>
                                </div>
                                <div className="textoContenedor">
                                    <a className="texto">React</a>
                                    <a className="texto">Bootstrap</a>
                                </div>
                            </div>

                        </div>
                    </div>

                    <div className="tarjeta sombra">
                        <div className="tituloContenedor">
                            <div className="titulo">{t("habilidades.titulos.backEnd")}</div>
                        </div>
                        <div className="areas">
                            <div className="area">
                                {t("habilidades.areas.lenguajes")}
                            </div>
                            <div className="tecnologias">
                                <div className="iconosContenedor">
                                    <img  className="icono" src={Java} alt="logo Java"></img>
                                    <img  className="icono" src={MySQL} alt="logo MySql"></img>
                                </div>
                                <div className="textoContenedor">
                                    <a className="texto">Java</a>
                                    <a className="texto">MySQL</a>
                                </div>
                            </div>

                            <div className="area">
                                {t("habilidades.areas.frameworks")}
                            </div>
                            <div className="tecnologias">
                                <div className="iconosContenedor">
                                    <img  className="icono pequeno" src={Springboot} alt="logo html"></img>
                                </div>
                                <div className="textoContenedor">
                                    <a className="texto">Springboot</a>
                                </div>
                            </div>

                        </div>
                    </div>

                    <div className="tarjeta sombra">
                        <div className="tituloContenedor">
                            <div className="titulo">{t("habilidades.titulos.otrasTecnologias")}</div>
                        </div>
                        <div className="areas">
                            <div className="area">
                            {t("habilidades.areas.entorno")}
                            </div>
                            <div className="tecnologias">
                                <div className="iconosContenedor">
                                    <img  className="icono grande" src={NodeJS} alt="logo nodeJS"></img>
                                </div>
                                <div className="textoContenedor">
                                    <a className="texto">Node JS</a>
                                </div>
                            </div>

                            <div className="area">
                                {t("habilidades.areas.controlDeVersiones")}
                            </div>
                            <div className="tecnologias">
                                <div className="iconosContenedor">
                                    <img  className="icono grande" src={Git} alt="logo Git"></img>
                                    <img  className="icono" src={GitBash} alt="logo GitBash"></img>
                                    <img  className="icono" src={GitHub} alt="logo GitHub"></img>
                                </div>
                                <div className="textoContenedor">
                                    <a className="texto">Git</a>
                                    <a className="texto">GitBash</a>
                                    <a className="texto">GitHub</a>
                                </div>
                            </div>

                            <div className="area">
                                {t("habilidades.areas.gestionDeProyectos")}
                            </div>
                            <div className="tecnologias">
                                <div className="iconosContenedor">
                                    <img  className="icono grande" src={Jira} alt="logo Jira"></img>
                                </div>
                                <div className="textoContenedor">
                                    <a className="texto">Jira</a>
                                </div>
                            </div>

                            <div className="area">
                            {t("habilidades.areas.maquetado")}
                            </div>
                            <div className="tecnologias">
                                <div className="iconosContenedor">
                                    <img  className="icono" src={Figma} alt="logo Figma"></img>
                                </div>
                                <div className="textoContenedor">
                                    <a className="texto">Figma</a>
                                </div>
                            </div>

                            <div className="area">
                                {t("habilidades.areas.otros")}
                            </div>
                            <div className="tecnologias">
                                <div className="iconosContenedor">
                                    <img  className="icono" src={Consola} alt="logo Consola"></img>
                                    <img  className="icono" src={Sass} alt="logo Sass"></img>
                                </div>
                                <div className="textoContenedor">
                                    <a className="texto">Console</a>
                                    <a className="texto">Sass</a>
                                </div>
                            </div>

                        </div>
                    </div>
                    </div>
                </div>

                <div className="blandas">
                    <h3 className="habilidad">{t("habilidades.habilidades.blandas")}</h3>
                    
                    <div className="tarjetasContenedor">
                        <div className="tarjeta-grande sombra">
                            <div className="areas">
                                <div className="area">
                                    {t("habilidades.areas.metodologia")}
                                </div>
                                <div className="tecnologias">
                                    <div className="textoContenedor">
                                        <a className="texto">Agile</a>
                                        <a className="texto">SCRUM</a>
                                    </div>
                                </div>

                                <div className="area">
                                    {t("habilidades.areas.fortalezas")}
                                </div>
                                <div className="tecnologias">
                                    <div className="textoContenedor">
                                        <p className="texto">
                                            {t("habilidades.habilidadesBlandasTexto.comunicacion")}
                                            <br/>
                                            {t("habilidades.habilidadesBlandasTexto.adaptabilidad")}
                                            <br/>
                                            {t("habilidades.habilidadesBlandasTexto.creatividad")}
                                            <br/>
                                            {t("habilidades.habilidadesBlandasTexto.atencionAlDetalle")}
                                            <br/>
                                            {t("habilidades.habilidadesBlandasTexto.comunicacionInterpersonal")}
                                            <br/>
                                            {t("habilidades.habilidadesBlandasTexto.confianza")}
                                            <br/>
                                            {t("habilidades.habilidadesBlandasTexto.perseverancia")}
                                            <br/>
                                            {t("habilidades.habilidadesBlandasTexto.resolucionDeProblemas")}
                                            <br/>
                                            {t("habilidades.habilidadesBlandasTexto.trabajoEnEquipo")}
                                            <br/>
                                            {t("habilidades.habilidadesBlandasTexto.razonamientoLogico")}
                                            <br/>
                                            {t("habilidades.habilidadesBlandasTexto.tomaDeDecisiones")}
                                            <br/>
                                            {t("habilidades.habilidadesBlandasTexto.pensamientoEstrategico")}
                                            <br/>
                                            {t("habilidades.habilidadesBlandasTexto.profesionalismo")}
                                            <br/>
                                            {t("habilidades.habilidadesBlandasTexto.liderazgo")}
                                            <br/>
                                            {t("habilidades.habilidadesBlandasTexto.organizacion")}
                                            <br/>
                                            {t("habilidades.habilidadesBlandasTexto.manejoDelTiempo")}
                                            <br/>
                                            {t("habilidades.habilidadesBlandasTexto.inteligenciaEmocional")}
                                            <br/>
                                            {t("habilidades.habilidadesBlandasTexto.servicioAlCliente")}
                                        </p>
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
            
    </div>
    </>
}

export default Habilidades;