import header from "../recursos/header.jpg";
import profilePic from "../recursos/profile.jpeg";
import { useTranslation } from "react-i18next";

export default function Encabezado(){
    //Se encarga de obtener el idioma a usar en el texto
    const [t, i18n] = useTranslation("global");

    return <>
        <div id="Encabezado" className="encabezado">
            <img className="imagenEncabezado" alt="laptop and a coffee mug on top of a desk" src={header}></img>
            <div className="nombreContenedor">
                <p className="nombre">Héctor Rangel</p>
                <p className="titulo">{t("encabezado.titulo")}</p>
            </div>
            <div className="contenedorImagen">
                <img className="imagenPerfil" src={profilePic} alt="profile picture"></img>
            </div>
        </div>
    </>
}