import React from "react";
import Creditos from "./Creditos";
import mx from "./../recursos/iconos/Banderas/mx.svg";
import us from "./../recursos/iconos/Banderas/us.svg";
import { useTranslation } from "react-i18next";

export default function BarraNavegacion(){
    const[creditos, setCreditos] = React.useState(false);
    const[mostrarBanderas, setMostrarBanderas] = React.useState(false);

    //Se encarga de obtener el idioma a usar en el texto
    const [t, i18n] = useTranslation("global");

    const cambiarIdioma=(idioma:string)=>{
        setMostrarBanderas(false);
        i18n.changeLanguage(idioma);
    }

    return <>
        {creditos===true ? 
            <div className="creditos">
                <svg xmlns="http://www.w3.org/2000/svg" width="30" height="30" fill="currentColor" className="cerrar bi bi-x-circle" viewBox="0 0 16 16" onClick={()=>setCreditos(false)}>
                    <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14m0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16"/>
                    <path d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708"/>
                </svg>
                <Creditos/>
            </div>
            : 
            <></>
        }
        <div className="contenedorBarraNavegacion">
            <div className="barraNavegacion">
                <a href="/#Encabezado" className="seccion">{t("barraNavegacion.inicio")}</a>
                <a href="/#Proyectos" className="seccion">{t("barraNavegacion.proyectos")}</a>
                <a href="/#Educacion" className="seccion">{t("barraNavegacion.educacion")}</a>
                <a href="/#Habilidades" className="seccion">{t("barraNavegacion.habilidades")}</a>
                <a href="/#Contacto" className="seccion">{t("barraNavegacion.contacto")}</a>
                <a href="/#Footer" className="seccion">{t("barraNavegacion.redes")}</a>
                <a onClick={()=>setCreditos(true)}className="seccion">{t("barraNavegacion.creditos")}</a>
                <a onClick={()=>setMostrarBanderas(!mostrarBanderas)} className="seccion">{t("barraNavegacion.idioma")}</a>
            </div>
            {mostrarBanderas===true?
                <div className="banderasContenedor">
                    <div className="bandera"><img onClick={()=>{cambiarIdioma("es")}} className="banderaIcono" src={mx} alt="Mexico" ></img></div>
                    <div className="bandera"><img onClick={()=>{cambiarIdioma("en")}} className="banderaIcono" src={us} alt="US"></img></div>
                </div> : <></>
            }
        </div>
        
    </>
}
