import { useTranslation } from "react-i18next";

export default function AcercaDe(){
    //Se encarga de obtener el idioma a usar en el texto
    const [t, i18n] = useTranslation("global");

    return <>
        <div id="AcercaDe" className="acercaDeContenedor">
            <h2 className="subtitulo">{t("acercaDe.subtitulo")}</h2>
            <p className="descripcion">
                {t("acercaDe.descripcion")}
            </p>
        </div>
    </>
}