import React from "react";
import { useTranslation } from "react-i18next";
import Carrusel from "./Carrusel";

import generation from "../recursos/Certificaciones/Generation.jpg";
import generation2 from "../recursos/Certificaciones/Generation2.jpg";
import alura from "../recursos/Certificaciones/Alura.jpg";
import curador from "../recursos/Certificaciones/CuradorDatos.jpg"
import javaScriptLI from "../recursos/Certificaciones/JavaScriptLI.jpg";
import javaLI from "../recursos/Certificaciones/JavaLI.jpg";
import closeIcon from "../recursos/iconos/close.svg";

export default function Educacion(){
     //Se encarga de obtener el idioma a usar en el texto
     const [t, i18n] = useTranslation("global");

    const [abrirCertificado, setAbrirCertificado] = React.useState(false);
    const [certificadoIndex, setCertificadoIndex] = React.useState(0);

    const descripciones=[];

    descripciones.push(t("educacion.descripcion.descGeneration"));
    descripciones.push(t("educacion.descripcion.descGeneration"));
    descripciones.push(t("educacion.descripcion.descAlura"));
    descripciones.push(t("educacion.descripcion.descFundacionCS"))
    descripciones.push(t("educacion.descripcion.descLinkedIn"));
    descripciones.push(t("educacion.descripcion.descLinkedIn"));
  
    //Arreglo con cada imagen del carrusel
    const imagenes=[generation, generation2, alura, curador, javaScriptLI, javaLI];
    //Arreglo con cada descripción del carrusel

    const AbrirCertificado =(e : number)=>{
        setAbrirCertificado(true);
        setCertificadoIndex(e);
        console.log(e);
    }

    return <>
    <div id="Educacion" className="educacionContenedor apartado"> 
        <h2 className="subtitulo">{t("educacion.subtitulo")}</h2>
        <div className="certificadosContenedor">
            <img className="certificado sombra" src={imagenes[0]} alt="" onClick={()=>AbrirCertificado(0)}></img>
            <img className="certificado sombra" src={imagenes[1]} alt="" onClick={()=>AbrirCertificado(1)}></img>
            <img className="certificado sombra" src={imagenes[2]} alt="" onClick={()=>AbrirCertificado(2)}></img>
            <img className="certificado sombra" src={imagenes[3]} alt="" onClick={()=>AbrirCertificado(3)}></img>
            <img className="certificado sombra" src={imagenes[4]} alt="" onClick={()=>AbrirCertificado(4)}></img>
            <img className="certificado sombra" src={imagenes[5]} alt="" onClick={()=>AbrirCertificado(5)}></img>
        </div>

        {abrirCertificado==true ? 
            <div className="certificadoCompletoContenedor">
                <div className="certificadoZoomContenedor">
                    <div className="cerrarContenedor"> 
                        <img src={closeIcon} id="cerrar" onClick={()=>setAbrirCertificado(false)}></img>
                    </div>
                    <div id="mostrarCarrusel">
                        <Carrusel indice={certificadoIndex} imagenes={imagenes} descripciones={descripciones} ancho={600}/>
                    </div>
                </div>
            </div>
            : 
            <></>
        }
    </div>
    </>
}

